					                    		  <input class="form-control" type="number" required="" name="harga_beli" v-model="percetakan.harga_beli">
								                </div>
					                    	</div>
					                    	<div class="col">
					                    		<span>Harga Jual</span>
					                    		<div class="input-group mb-3">
								                  <div class="input-group-prepend">
								                    <div type="button" class="btn btn-secondary">Rp.</div>
								                  </div>
								                  <!-- /btn-group -->
					                    		  <input class="form-control" type="number" required="" name="harga_jual" v-model="percetakan.harga_jual">
								                </div>
					                    	</div>
					                    </div>
					                </div>
				                	<div class="form-group">
					                    <label>Stok <span class="text-danger">*</span></label>
					                    <div class="row">
					                    	<div class="col">
					                    		<span>Stok Awal</span>
					                    		<div class="input-group mb-3">
								                  <div class="input-group-prepend">
								                    <div type="button" class="btn btn-secondary">Rp.</div>
								                  </div>
								                  <!-- /btn-group -->
					                    		  <input class="form-control" type="number" required="" name="stok_awal" v-model="percetakan.stok_awal">
								                </div>
					                    	</div>
					                    	<div class="col">
					                    		<span>Stok Akhir</span>
					                    		<div class="input-group mb-3">
								                  <div class="input-group-prepend">
								                    <div type="button" class="btn btn-secondary">Rp.</div>
								                  </div>
								                  <!-- /btn-group -->
					                    		  <input class="form-control" type="number" required="" name="stok_akhir" v-model="percetakan.stok_akhir">
								                </div>
					                    	</div>
					                    </div>
					                </div>
				                	<div class="form-group">
				                		<label for="expired_date">Expired Date <span class="text-danger">*</span></label>
				                		<div class="row">
				                			<div class="col col-md-4">
				                				<input type="date" class="form-control" id="expired_date" name="expired_date" v-model="percetakan.expired_date" required="">
				                			</div>
				                			<div class="col col-md-8"></div>
				                		</div>
				                	</div>
				                	<div class="form-group">
				                		<div class="row">
				                			<div class="col">
				                				<label for="minimal_stok">Minimal Stok <span class="text-danger">*</span> </label>
				                				<input type="text" class="form-control" id="minimal_stok" name="minimal_stok" autocomplete="off" v-model="percetakan.minimal_stok" required="">	
				                			</div>
				                			<div class="col">
				                				<label for="maksimal_stok">Maksimal Stok <span class="text-danger">*</span></label>
				                				<input type="text" class="form-control" id="maksimal_stok" name="maksimal_stok" autocomplete="off" v-model="percetakan.maksimal_stok" required="">
				                			</div>
				                		</div>
				                	</div>
				                	<div class="form-group">
				                		<label for="lokasi">Lokasi <span class="text-danger">*</span></label>
				                		<input type="text" class="form-control" id="lokasi" name="lokasi" autocomplete="off" v-model="percetakan.lokasi" required="">
				                	</div>
				                	<div class="form-group">
					                    <label>Fee <span class="text-danger">*</span></label>
					                    <div class="row">
					                    	<div class="col">
					                    		<span>Fee Dokter</span>
					                    		<div class="input-group">
								                  <div class="input-group-prepend">
								                    <div type="button" class="btn btn-secondary">Rp.</div>
								                  </div>
								                  <!-- /btn-group -->
					                    		  <input class="form-control" type="number" required="" name="fee_dokter" v-model="percetakan.fee_dokter">
								                </div>
					                    	</div>
					                    	<div class="col">
					                    		<span>Fee Beautician</span>
					                    		<div class="input-group">
								                  <div class="input-group-prepend">
								                    <div type="button" class="btn btn-secondary">Rp.</div>
								                  </div>
								                  <!-- /btn-group -->
					                    		  <input class="form-control" type="number" required="" name="fee_beautician" v-model="percetakan.fee_beautician">
								                </div>
					                    	</div>
					                    	<div class="col">
					                    		<span>Fee Sales</span>
					                    		<div class="input-group">
								                  <div class="input-group-prepend">
