							<!-- <p class="text-center mt-4">{{ cekData }}</p> -->
	            		</div>
					</div>
				</div>
			</div>
		</div>
	</div>
		
	</div>
</template>

<script>
	import ContentHeader from '@/components/ContentHeader2'

	import { computed, reactive, onMounted, ref } from 'vue'
	import store from '@/store'
	import axios from 'axios'
	import Swal from 'sweetalert2'
	import tambah_nol from '@/tambah_nol'
	import router from '@/router'

	export default{
		components: {
			ContentHeader: ContentHeader
		},
		setup() {
			const user = computed(() => store.getters['auth/user'])
			const loading = ref(false)
			const percetakan = reactive({
				"cabang_id" : "",
				"kode" : "",
				"nama" : "",
				"kategori" : "",
				"jenis" : "",
				"fungsi" : "",
				"golongan" : "",
				"satuan" : "",
				"harga_beli" : "",
				"harga_jual" : "",
				"stok_awal" : "",
				"stok_akhir" : "",
				"expired_date" : "",
				"minimal_stok" : "",
				"maksimal_stok" : "",
				"lokasi" : "",
				"fee_dokter" : "",
				"fee_beautician" : "",
				"fee_sales" : "",
				"supplier_id" : "",
			})

			const { generateKode, generateKodeDanGetNomorUrut, getCabang } = useGenerateKode(user, percetakan) 

			const prosesTambahPercetakan = () => {
				loading.value = true
				axios.post(`api/percetakan/create`, percetakan)
				.then((response) => {
					Swal.fire({
					  title: 'Berhasil!',
					  text: 'Berhasil menambahkan data',
					  icon: 'success',
					  showConfirmButton: false,
					  timer: 1500
					})	
					loading.value = false
					router.push('/data-percetakan')
					console.log(response)
				})
				.catch((error) => {
					Swal.fire({
					  title: 'Gagal!',
					  text: 'Gagal menambahkan data',
					  icon: 'error',
					  confirmButtonText: 'Ok'
					})	
					loading.value = false
					console.log(error)
				})
			}


			onMounted(() => {
				getCabang()
			})

			return {
				percetakan, generateKode, generateKodeDanGetNomorUrut, loading, prosesTambahPercetakan
			}
		}
	}

	function useGenerateKode (user, percetakan) {
		const kode_cabang = ref()
		const nomor_urut = ref('')

		const getCabang = async () => {
			await axios.get(`api/cabang/getWhereId/${user.value.cabang_id}`)
	    	.then((response) => {
	    		kode_cabang.value = `${response.data.kode_cabang}`
	    		percetakan.cabang_id = response.data.id
	    	}) 
	    	.catch((error) => {
	    		console.log(error)
	    	})

	    	percetakan.kode = `${kode_cabang.value}-`
		}

		const getNomorUrut = async () => {
			await axios.get(`api/percetakan/getForGenerateCode/${percetakan.kode}`)
	    	.then((response) => {
	    		if(response.data == 'kosong'){
	    			nomor_urut.value = tambah_nol(1)
	    			generateKode()
	    		} else {
	    			let str = response.data
					let length = str.length
					let nlength = length - 3
					let id = str.slice(nlength, length) 
	    			id = Math.abs(id)
	    			nomor_urut.value = tambah_nol(id + 1)
	    			generateKode()
	    		}
	    	})
	    	.catch((error) => {
	    		console.log(error)
	    	})
		}

		const generateKode = () => {
			percetakan.kode = `${kode_cabang.value}-${percetakan.kategori}${percetakan.jenis}${nomor_urut.value}`
		}

		const generateKodeDanGetNomorUrut = () => {
			getNomorUrut()

			percetakan.kode = `${kode_cabang.value}-${percetakan.kategori}${percetakan.jenis}${nomor_urut.value}`
		}

		return {
			generateKode, generateKodeDanGetNomorUrut, getCabang
		}
	}
</script>